* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #fff;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  /* background: radial-gradient(
    circle at center,
    rgba(255, 153, 178, 0.5) 20%,
    rgba(255, 240, 245, 0.9) 40%,
    rgba(255, 250, 250, 1) 70%);
    background-size: cover;
    background-position: center top; */
  
}

.navbar {
  width: 100%;
  max-width: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 8px 10px;
  background: transparent !important;
  border-bottom: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.App {
  padding-top: 0px;
  margin-top: 0;
  background: #fff;
  min-height: 100vh;
}

/* default nav links on desktop */
.nav-links {
  display: flex;
  gap: 20px;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 25px;
  padding: 5px 15px;
}

.navbar a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
  padding: 8px 16px;
  transition: all 0.3s ease;
  background: none !important;
}

.navbar a:hover {
  color: #afcaa6;
  background: none !important;
}

.navbar a:active {
  background: none !important;
  color: white;
}

/* hidden by default (show on mobile) */
.hamburger {
  display: none;
  cursor: pointer;
  flex-direction: column;
  gap: 5px;
  z-index: 1100;
}

.hamburger .bar {
  width: 25px;
  height: 3px;
  background-color: #333;
  transition: all 0.3s ease;
}

.hamburger.open .bar:nth-child(1) {
  transform: translateY(6px) rotate(45deg);
}

.hamburger.open .bar:nth-child(2) {
  opacity: 0;
}

.hamburger.open .bar:nth-child(3) {
  transform: translateY(-6px) rotate(-45deg);
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  z-index: 999;
}

.overlay.show {
  display: block;
}

.main-content {
  margin-top: 0px;
  padding-top: 80px;
}

.paypal-form {
  position: relative;
  display: inline-block;
}

.paypal-form a::after {
  content: "Support me with a donation!";
  position: absolute;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  color: #fff;
  padding: 6px 10px;
  border-radius: 5px;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
  font-size: 12px;
  z-index: 10;
}

.paypal-form a:hover::after {
  opacity: 1;
}

.paypal-button {
  height: 32px;
  cursor: pointer;
  transition: transform 0.2s;
}

.paypal-button:hover {
  transform: scale(1.1);
}

/* Privacy Policy */
.background-gradient {
  width: 90%;
  height: 100vh;
  margin: 0 auto;
  padding: 60px;
  background: radial-gradient(
    circle at center,
    rgba(255, 153, 178, 0.5) 20%,
    rgba(255, 240, 245, 0.9) 40%,
    rgba(255, 250, 250, 1) 60%);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  animation: pulse 5s infinite alternate ease-in-out;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}


.privacy-container h1 {
  font-family: 'Poppins', sans-serif;
  font-size: 2.5rem;
  margin-bottom: 25px;
}

.privacy-container p {
  font-family: 'Poppins', sans-serif;
  font-size: 1.1rem;
  line-height: 1.6;
  color: #333;
  margin-bottom: 20px;

}


.privacy-container a:hover {
  color: pink; /* Pink hover effect */
  text-decoration: underline;
}

/* Contact */
.contact-container {
  width: 100%;
  padding: 20px;
  height: 100vh;
  background: radial-gradient(
    circle at center,
    rgba(255, 153, 178, 0.5) 20%,
    rgba(255, 240, 245, 0.9) 40%,
    rgba(255, 250, 250, 1) 70%); 
  overflow: hidden;
  animation: pulse 5s infinite alternate ease-in-out;
  background-repeat: no-repeat;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

/* Contact Form */
.contact-form {
  width: 400px;
  padding: 30px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}


.contact-form h2 {
  text-align: center;
  margin-bottom: 20px;
  font-family: 'Arial', sans-serif;
  color: #333;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
  margin-bottom: 15px;
}

.contact-form textarea {
  height: 120px;
  resize: none;
}

.contact-form button {
  margin-top: 1.5rem;
  padding: 12px 24px;
  font-size: 1.2rem;
  border: 2px solid black;
  border-radius: 25px;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  text-align: center;
}

.contact-form button:hover {
  background-color: #ff69b4;
  color: white;
  transform: scale(1.05);
}

.status-message {
  margin-top: 15px;
  text-align: center;
  font-weight: bold;
  font-size: 0.9rem;
  color: pink;
}




@keyframes pulse {
