/* General Button Styling */
button {
  background: transparent;
  color: black;
  border: 2px solid black;
  border-radius: 25px;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  margin: 10px 0;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:not(:disabled):hover {
  background: linear-gradient(135deg, #6cb2eb, #3490dc);
  color: white;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  border-color: #999;
  color: #666;
}

button:hover {
  background: linear-gradient(135deg, #6cb2eb, #3490dc) !important;
  color: white;
  transform: scale(1.05);
}

button:active {
  transform: scale(0.98);
  background: linear-gradient(135deg, #3490dc, #6cb2eb);
}

/* Color Picker Options */
.color-options {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 15px;
}

/* Fix Layout Between Camera and Photo Strip */
.photo-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 30px; 
  width: 100%;
  max-width: 1200px;
  margin: 20px auto;
}

/* Fix Photo Strip Thumbnails */
.preview-side {
  display: flex;
  margin-left: 20px;
}

/* Vertical Layout (A, B, C) */
.preview-side-vertical {
  flex-direction: column;
  gap: 10px;
  justify-content: center;
}

/* Grid Layout (D) */
.preview-side-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 10px;
  width: 340px;
  max-width: 100%;
}

/* Layout-specific styling */
.preview-side.poses-4 .side-preview,
.preview-side.poses-4 .side-preview-placeholder {
  width: 160px;
  height: 120px;
}

.preview-side.poses-3 .side-preview,
.preview-side.poses-3 .side-preview-placeholder {
  width: 160px;
  height: 140px;
}

.preview-side.poses-2 .side-preview,
.preview-side.poses-2 .side-preview-placeholder {
  width: 160px;
  height: 160px;
}

.grid-2x3 .side-preview,
.grid-2x3 .side-preview-placeholder {
  width: 160px;
  height: 120px;
}

/* Placeholder styling */
.side-preview-placeholder {
  background-color: #f5f5f5;
  border: 1px dashed #ccc;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.side-preview-placeholder::before {
  content: attr(data-number);
  font-size: 20px;
  color: #aaa;
  font-weight: bold;
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .preview-side-grid {
    width: 280px;
    margin: 20px auto;
  }
  
  .preview-side-vertical {
    margin: 20px auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  
  .preview-side.poses-2 .side-preview,
  .preview-side.poses-2 .side-preview-placeholder,
  .preview-side.poses-3 .side-preview,
  .preview-side.poses-3 .side-preview-placeholder,
  .preview-side.poses-4 .side-preview,
  .preview-side.poses-4 .side-preview-placeholder,
  .grid-2x3 .side-preview, 
  .grid-2x3 .side-preview-placeholder {
    width: 120px;
    height: 90px;
  }
}

@media (max-width: 576px) {
  .grid-2x3 .side-preview,
  .grid-2x3 .side-preview-placeholder {
    width: 80px;
    height: 60px;
  }
  
  .preview-side.poses-2 .side-preview,
  .preview-side.poses-2 .side-preview-placeholder,
  .preview-side.poses-3 .side-preview,
  .preview-side.poses-3 .side-preview-placeholder,
  .preview-side.poses-4 .side-preview,
  .preview-side.poses-4 .side-preview-placeholder {
    width: 80px;
    height: 60px;
  }
  
  .preview-side-grid {
    width: 180px;
    gap: 5px;
  }
  
  .preview-side-vertical {
    gap: 5px;
  }
}

